.burger {
  position: fixed;
  top: 3rem;
  left: 4rem;
  z-index: 3;
}

.category {
  position: fixed;
  height: 2000px;
  width: 100%;
  background: rgba(44, 0, 22, 0.61);
  z-index: 2;
}

.category>p {
  font-size: 1rem;
  font-family: monospace;
  position: absolute;
  top: 8rem;
  left: 8rem;
}

.pond {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 2600px;
  overflow: hidden;
}

.sound {
  opacity: 0.8;
  position: absolute;
  top: 11.5%;
  left: 6.5%;
  transition: 0.5s;
  z-index: 3;
}

.sound:hover {
  opacity: 0.95;
}

.fish {
  position: absolute;
  animation: move-post 100s infinite;
  transition: 0.5s;
}

.fish:hover {
  cursor: pointer;
  opacity: 0.8;
}

.fish2 {
  position: absolute;
  animation: move-post2 80s infinite;
  transition: 0.5s;
}

.fish2:hover {
  cursor: pointer;
  opacity: 0.8;
}

.fish3 {
  position: absolute;
  animation: move-post3 90s infinite;
  transition: 0.5s;
}

.fish3:hover {
  cursor: pointer;
  opacity: 0.8;
}

.content {
  height: 200px;
  width: 100%;
  position: fixed;
  top: 35%;
}

.title {
  text-align: center;
  font-weight: 900;
  font-size: 8rem;
  padding: 0px;
  margin: 0px;
  color: rgba(255, 255, 255, 0.9);
  animation: title 10s infinite alternate;
}

.cursorText {
  position: absolute;
  padding: 0px;
  width: 100%;
  height: 30px;
  font-size: 1.5rem;
  color: rgb(255, 255, 255);
  font-family: monospace;
  text-align: center;
}

.cursor {
  display: inline-block;
  position: relative;
  top: -3rem;
}

.cursor::after {
  content: '';
  margin: auto;
  position: absolute;
  right: -10px;
  top: 1px;
  width: 10px;
  height: 80%;
  background-color: rgb(255, 255, 255);
  animation: cursor-animation 1.5s step-end infinite;
}

@keyframes cursor-animation {
  0% {
    opacity: 0;
  }

  50% {
    opacity: 1;
  }

  100% {
    opacity: 0;
  }
}

.modal-wrapper {
  position: fixed;
  width: 100%;
  height: 100%;
  display: flex;
}

.modal {
  position: absolute;
  bottom: 4%;
  right: 2%;
  color: rgba(255, 192, 203, 0.95);
  background: rgba(255, 255, 255, 0.95);
  font-size: 1rem;
  padding: 20px 24px;
  border-radius: 4px;
  width: 320px;
}

.modal-title {
  font-size: 2rem;
  font-weight: bold;
}

.close-wrapper {
  text-align: right;
}

#close {
  background: rgba(255, 192, 203, 0.95);
  color: rgba(255, 255, 255, 0.95);
  border: 0px;
  padding: 5px 20px
}

#close:hover {
  background: rgba(255, 133, 153, 0.95);
  cursor: pointer;
}

.colorset {
  position: absolute;
  top: 0px;
  left: 0px;
  width: 100%;
  display: inline-flex;
  flex-wrap: wrap;
  z-index: -1;
}

